@font-face {
  font-family: "FiraSans-Regular";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local("FiraSans-Regular"), url("../fonts/FiraSans-Regular.woff2") format("woff2"), url("../fonts/FiraSans-Regular.woff") format("woff"); }

@font-face {
  font-family: "FiraSans-Medium";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: local("FiraSans-Medium"), url("../fonts/FiraSans-Medium.woff2") format("woff2"), url("../fonts/FiraSans-Medium.woff") format("woff"); }

@font-face {
  font-family: "FiraSans-Bold";
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: local("FiraSans-Bold"), url("../fonts/FiraSans-Bold.woff2") format("woff2"), url("../fonts/FiraSans-Bold.woff") format("woff"); }

@font-face {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: local("Montserrat-Bold"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  min-width: 320px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
  color: #484848;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: relative;
  padding-top: 32px;
  z-index: 5; }

.page-header__wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  width: 83.6%;
  margin: 0 auto; }

.page-header__phone {
  font-family: "FiraSans-Medium", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: #484848; }

.page-header__logo {
  display: block;
  padding-right: 24px; }

.page-header__link {
  position: relative;
  margin-right: 3px;
  font-family: "FiraSans-Medium", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-variant: small-caps;
  color: #484848; }
  .page-header__link::before {
    position: absolute;
    content: "";
    top: 3px;
    left: 0;
    width: 100%;
    height: 100%;
    border-bottom: 2px dashed #484848; }

@media (max-width: 410px) {
  .page-header {
    padding-top: 20px;
    padding-bottom: 20px; }
  .page-header__wrap {
    width: 95%; }
  .page-header__phone {
    margin-top: -25px;
    font-size: 14px;
    line-height: 17px; }
  .page-header__logo {
    padding-left: 8px;
    order: -1; }
  .page-header__link {
    margin: 0 0 0 auto;
    margin-top: -18px;
    font-size: 14px;
    line-height: 17px; }
    .page-header__link::before {
      top: 2px; } }

.promo {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: -80px;
  height: 878px;
  background-image: url("../img/header-girl.png"), url("../img/header-text.png"), url("../img/header-backdrop.jpg");
  background-repeat: no-repeat;
  background-position: center bottom 95%, center 160px, center;
  background-size: cover, 74.5%, cover; }

.promo__title {
  max-width: 343px;
  margin: auto auto 281px;
  font-family: "FiraSans-Bold", "Arial", sans-serif;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12); }

.promo__btn {
  position: absolute;
  left: 50%;
  bottom: 185px;
  width: 54px;
  height: 54px;
  margin-left: -26px;
  background-image: url("../img/mouse.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 50%;
  cursor: pointer; }
  .promo__btn::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: -11px;
    border: 1px dashed #ffffff;
    border-radius: 50%; }
  .promo__btn::after {
    position: absolute;
    content: "";
    top: 63px;
    width: 0;
    height: 55.5px;
    border: 1px solid #ffffff; }

@media (max-width: 767px) {
  .promo {
    height: 536px;
    margin-top: 0;
    background-image: url("../img/header-girl-mobile.png"), url("../img/header-text-mobile.png"), url("../img/header-backdrop-mobile.jpg");
    background-position: center -51px, center 85px, center -50px;
    background-size: cover, contain, cover; }
  .promo__title {
    margin-bottom: 235px;
    font-size: 20px;
    line-height: 24px; }
  .promo__btn {
    bottom: 138px; } }
